import React,{useState,useEffect} from 'react'
import { message } from 'antd'
import axios from "axios"

const RegresionLogisticaResults=({variablesSelected,corrVariables,setLoading,classVariable,otherRegressionVariables})=>{

    const [responseData,setResponseData]=useState({})

    const url="/api/regresion-logistica-model"
    const file=new FormData()
    const jsonVariables=JSON.stringify({variablesSelected:variablesSelected,classVariable:classVariable,otherRegressionVariabels:otherRegressionVariables})
    file.append("file",corrVariables.file)
    file.append("variablesSelected",jsonVariables)


    useEffect(() => {
        axios.post(url,file,{headers: {'Content-Type': 'multipart/form-data'}}).then(response=>{
            setResponseData(response.data.model)
            setLoading(false)
        })
        .catch(error=>{
            message.error("Hubo un error, intentalo de nuevo")
            console.log(error)
            setLoading(false)
        })
    }, [])

    
    return(
        <div>
            {
                responseData.model
            }
        </div>
    )
}

export default RegresionLogisticaResults